.login {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login__logo {
  margin: 20px auto;
  object-fit: contain;
  width: 100px;
}

.login__container {
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgrey;
  padding: 20px;
}

.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}

.login__container > form > h5 {
  margin-bottom: 5px;
}

.login__container > form > input {
  height: 30px;
  width: 98%;
  margin-bottom: 10px;
  background-color: white;
}

.login__container > p {
  margin-top: 15px;
  font-size: 12px;
}

.signIn__title {
  text-align: center;
}

.login__signInButton {
  background-color: #f0c14b;
  border-radius: 2px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  width: 100%;
  height: 30px;
}

.login__signInButton:hover {
  opacity: 0.75;
}

.login__signInButton:active {
  background-color: #f0c14b;
  box-shadow: 0 2px #f0c14b;
  transform: translateY(1px);
}

.login__registerButton {
  border-color: darkgray;
  border-radius: 2px;
  border: 1px solid;
  margin-top: 10px;
  width: 100%;
  height: 30px;
}

.login__registerButton:hover {
  opacity: 0.75;
}

.login__registerButton:active {
  opacity: 0.75;
  background-color: darkgray;
  box-shadow: 0 2px darkgray;
  transform: translateY(1px);
}
